import React, { useState } from "react";
import "./Footer.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelope } from "@fortawesome/free-solid-svg-icons";
import {
  faLinkedin,
  faXing,
  faGithub,
  faTelegram,
  faKaggle,
} from "@fortawesome/free-brands-svg-icons";

function Footer(props) {
  const [loaded, setLoaded] = useState(false);

  return (
    <footer className={"footer" + " " + props.className}>
      <div>
        {!loaded ? (
          <img
            src="./images/dave-hoefler-wINEMQBBOGE-unsplash_low_res.jpg"
            alt="background_image_low_res"
            className="background_image"
          />
        ) : null}
      </div>
      <img
        src="./images/dave-hoefler-wINEMQBBOGE-unsplash.jpg"
        alt="background_image"
        className="background_image"
        style={!loaded ? { visibility: "hidden" } : {}}
        onLoad={() => setLoaded(true)}
      />
      <ul className="icons-list">
        <li>
          <a href="https://www.xing.com/profile/LisandroA_Cesaratto/">
            <FontAwesomeIcon icon={faXing} className="fa-icon" />
          </a>
        </li>
        <li>
          <a href="https://www.linkedin.com/in/lisandro-cesaratto/">
            <FontAwesomeIcon icon={faLinkedin} className="fa-icon" />
          </a>
        </li>
        <li>
          <a href="https://github.com/lcesaratto?tab=repositories">
            <FontAwesomeIcon icon={faGithub} className="fa-icon" />
          </a>
        </li>
        <li>
          <a href="https://www.kaggle.com/lisandroc">
            <FontAwesomeIcon icon={faKaggle} className="fa-icon" />
          </a>
        </li>
        <li>
          <a href="mailto:lisandro.cesaratto@outlook.com">
            <FontAwesomeIcon icon={faEnvelope} className="fa-icon" />
          </a>
        </li>
        <li>
          <a href="https://t.me/lisandrocesaratto">
            <FontAwesomeIcon icon={faTelegram} className="fa-icon" />
          </a>
        </li>
      </ul>
      {props.language.match("en") ? (
        <div className="footer-comment">
          Website created with React{" "}
          <img
            src="./images/logo.svg"
            alt="react-logo"
            className="react-logo"
          />{" "}
          by me
        </div>
      ) : props.language.match("de") ? (
        <div className="footer-comment">
          Von mir mit React{" "}
          <img
            src="./images/logo.svg"
            alt="react-logo"
            className="react-logo"
          />{" "}
          erstellte Website
        </div>
      ) : (
        <div className="footer-comment">
          Sitio web creado con React{" "}
          <img
            src="./images/logo.svg"
            alt="react-logo"
            className="react-logo"
          />{" "}
          por mi
        </div>
      )}
    </footer>
  );
}

export default Footer;
