import React, { useState } from "react";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import "./App.css";
import Header from "./components/Header";
import Projects from "./components/Projects";
import Awards from "./components/Awards";
import Skills from "./components/Skills";
import Footer from "./components/Footer";

function App() {
  const [page, setPage] = useState("projects");
  const [language, setLanguage] = useState("de");

  return (
    <div className="App">
      <Router>
        <Header
          className="App-header"
          page={page}
          setLanguage={setLanguage}
          language={language}
        />
        <section>
          <Switch>
            <Route
              path="/"
              exact
              render={() => (
                <div>
                  {setPage("projects")}
                  <Projects className="App-body" />
                </div>
              )}
            />
            <Route
              path="/awards"
              exact
              render={() => (
                <div>
                  {setPage("awards")}
                  <Awards className="App-body" />
                </div>
              )}
            />
            <Route
              path="/skills"
              exact
              render={() => (
                <div>
                  {setPage("skills")}
                  <Skills className="App-body" />
                </div>
              )}
            />
          </Switch>
        </section>
        <Footer className="App-footer" language={language} />
      </Router>
    </div>
  );
}

export default App;
