import React, { useState } from "react";
import "./Header.css";
import { Link } from "react-router-dom";

function Header(props) {
  const [loaded, setLoaded] = useState(false);

  return (
    <header className={"header" + " " + props.className}>
      <div className="language_tag">
        <a
          style={
            props.language.match("en")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
          onClick={() => {
            props.setLanguage("en");
          }}
        >
          EN
        </a>
        &nbsp;&nbsp;|&nbsp;&nbsp;
        <a
          style={
            props.language.match("de")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
          onClick={() => {
            props.setLanguage("de");
          }}
        >
          DE
        </a>
        &nbsp;&nbsp;|&nbsp;&nbsp;
        <a
          style={
            props.language.match("es")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
          onClick={() => {
            props.setLanguage("es");
          }}
        >
          ES
        </a>
      </div>
      <div>
        {!loaded ? (
          <img
            src="./images/dave-hoefler-wINEMQBBOGE-unsplash_low_res.jpg"
            alt="background_image_low_res"
            className="background_image"
          />
        ) : null}
      </div>
      <img
        className="background_image"
        src="./images/dave-hoefler-wINEMQBBOGE-unsplash.jpg"
        alt="background_image"
        style={!loaded ? { visibility: "hidden" } : {}}
        onLoad={() => setLoaded(true)}
      />
      <img
        src="./images/Lisandro_resbaja_web_Seeber_DSC4065-2.jpg"
        alt="personal_image"
        className="image"
      />

      {props.language.match("en") ? (
        <h1 className="presentation_text">
          <strong>Hi, I'm Lisandro Cesaratto,</strong>
          <br />
          an Electronic Engineer,
          <br />
          currently studying a
          <br />
          Data Science Master Degree
          <br />
        </h1>
      ) : props.language.match("de") ? (
        <h1 className="presentation_text">
          <strong>Hallo, ich bin Lisandro Cesaratto,</strong>
          <br />
          ein Elektroingenieur,
          <br />
          der zuerzeit ein
          <br />
          Master in Data Science absolviert
          <br />
        </h1>
      ) : (
        <h1 className="presentation_text">
          <strong>Hola, yo soy Lisandro Cesaratto,</strong>
          <br />
          un Ingeniero Electrónico,
          <br />
          actualmente realizando
          <br />
          un Máster en Ciencia de Datos
          <br />
        </h1>
      )}

      <ul className="links-list">
        <li
          style={
            props.page.match("projects")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
        >
          <Link exact="true" to="/">
            {props.language.match("en") ? (
              <div>Projects</div>
            ) : props.language.match("de") ? (
              <div>Projekte</div>
            ) : (
              <div>Proyectos</div>
            )}
          </Link>
        </li>
        <li
          style={
            props.page.match("awards")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
        >
          <Link exact="true" to="/awards">
            {props.language.match("en") ? (
              <div>Awards</div>
            ) : props.language.match("de") ? (
              <div>Auszeichnungen</div>
            ) : (
              <div>Distinciones</div>
            )}
          </Link>
        </li>
        <li
          style={
            props.page.match("skills")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
        >
          <Link exact="true" to="/skills">
            {props.language.match("en") ? (
              <div>Skills</div>
            ) : props.language.match("de") ? (
              <div>Fähigkeiten</div>
            ) : (
              <div>Habilidades</div>
            )}
          </Link>
        </li>
      </ul>
    </header>
  );
}

export default Header;
